.info{
  //background-image: url("../../assets/images/Background.png");
  background-color: #f4f8f9;
  border-radius: 10px;
  padding: 20px 25px;

  &__image{
    display: inline-block;
    vertical-align: top;
  }

  &__info{
    display: inline-block;
    padding-top: 10px;
    width: 350px;
    margin-left: 46px;
    font-size: 14px;
    font-family: Arial;
    color: rgb(102, 102, 102);
    font-style: italic;
    cursor: default;
  }

  &__telephone{
    display: inline-block;
    vertical-align: top;
    text-align: center;
    padding-top: 14px;
    padding-left: 15px;
  }

  &__block1{
    font-size: 12px;
    font-family: Arial;
    color: rgb(102, 102, 102);
    cursor: default;
  }

  &__block2{
    font-size: 18px;
    font-weight: bold;
    color: #2f4960;
    padding-top: 3px;
    cursor: default;
  }
}

.info-container{
  margin: 0 auto;
  width: 1000px;
}